import React from "react";
import { Route, Redirect } from "react-router-dom";

//        
const NotAuthentiCatedRoute = ({ component: Component, props: baseProps, ...rest }) =>{
  return(
    <Route
      {...rest} render={props =>
        baseProps.isLogin
          ? <Redirect to={'/'} /> 
          : <Component {...props} {...baseProps} />}
    />
  );
}

export default NotAuthentiCatedRoute;