import React, { useState, useEffect, Fragment } from "react";
import classNames from "classnames";
import axios from "axios";
import {
  Spinner,
  Container,
  Input,
  Button,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Table,
  InputGroup,
  InputGroupAddon,
} from "reactstrap";
import {
  BaseTable,
  Tbody,
  Tr,
  Td,
} from "react-row-select-table";
import "./TeamContent.css";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPen,
  faSearch
} from "@fortawesome/free-solid-svg-icons";


//    styled-components
const CustomTable = styled.div`
  table {
    border-collapse: inherit;
    border-spacing: 0px;
    width: 99% !important;
    /* float: right; */
    border-radius: 6px;
    border-width: 1px;
    border-style: solid;
    border-color: #c9c9c9;
    color: #505050 !important;
  }
  thead {
    float: left;
    width: 100%;
  }
  tbody {
    overflow-y: scroll !important;
    overflow-x: hidden;
    float: left;
    width: 100%;
    height: 300px;
  }
  tbody::-webkit-scrollbar {
    background-color: transparent !important;
  }
  tbody::-webkit-scrollbar-track {
    margin-top: 0px !important;
    margin-bottom: 0px !important;
    background-color: transparent !important;
  }
  tbody::-webkit-scrollbar-thumb {
    max-height: 100px !important;
    min-height: 100px !important;
    height: 100px;
    outline-color: transparent !important;
    outline-width: 0px !important;
  }
  tr {
    display: block;

    border-width: 0px 0px 1px 0px;
    border-style: solid;
    border-color: #c9c9c9;
    height: 50px;
    width: 100%;
  }
  tr:first-child {
    border-top-left-radius: 5px !important;
    border-top-right-radius: 5px !important;
  }
  tr:last-child {
    border-bottom-left-radius: 5px !important;
    border-bottom-right-radius: 5px !important;
    border-color: transparent;
  }
  tr.tr-body:hover {
    background-color: #f5f5f5;
  }

  tr.tr-checked {
    background-color: #c3d8ff !important;
  }

  th {
    width: 100px;
    padding: 0.5rem;
    text-align: left;
  }

  td {
    padding: 0.5rem;
    width: 300px;
    text-align: left;
  }
  td:first-child {
    width: 10px;
  }
  tbody input[type="checkbox"] {
    display: none;
  }
  tbody input[type="checkbox"]:checked {
    color: green !important;
    background-color: green !important;
  }
`;

const TeamContent = (props) => {
  const nickname=props.nickname; //,  
  const username=props.username; // ,  
// Modal On/Off
  const [teamAddModal, setTeamAddModal] = useState(false);
  const [teamInviteModal, setTeamInviteModal] = useState(false);
  const [teamModifyModal, setTeamModifyModal] = useState(false);
  const [teamLeaveModal, setTeamLeaveModal] = useState(false);
  const [teamDeleteModal, setTeamDeleteModal] = useState(false);


  const [teamList, setTeamList] = useState([]); // 
 
  const [myteamName, setMyTeamName] = useState(""); //    
  const [friendList, setFriendList] = useState([]); // 
  const [friendName, setFriendName] = useState(""); //   

  const friendChecked = []; //    
  const [currentTeamId, setCurrentTeamID] = useState(""); //   id
  const [currentTeamName, setCurrentTeamName] = useState("");//   name
  const [teamLeader, setTeamLeader] = useState(""); //    
  const [teamLeaderNick, setTeamLeaderNick] = useState(""); //  
  const [memberList, setMemberList] = useState([]); //  
  const [memberListNick, setMemberListNick] = useState([]); //   
  const [memberListId, setMemberListId] = useState([]); //  id
  const [isLeader, setIsLeader] = useState(false); //    
  const [renameTeamCheck, setRenameTeamCheck] = useState(false); //    on/off
  const [isLoading, setIsLoading] = useState(true);
  const [searchKeyword, setSearchKeyword] = useState('');

 

  useEffect(() => {
    loadTeamList();
  }, []);

  //axios config
  const option = {
    headers: {
      "Content-Type": "application/json",
    },
    withCredentials: true,
  };

  //  
  const loadTeamList = () => {
    console.log("load team!");
    const tempList1 = [];
    const tempList2 = [];
    axios
      .get(`${window.location.origin}/api/team`, option)
      .catch(error=>{
        props.errorCheck(error.response);
      }) 
      .then((content) => {
        content["data"].map((team, index) => {
          tempList1.push(team);
        });
        axios.get(`${window.location.origin}/api/join-team`, option)
          .catch(error=>{
            props.errorCheck(error.response);
          }) 
          .then((content) => {
            content["data"].map((team, index) => {

              tempList2.push(team);
            });
            const tempList3 = tempList1.concat(tempList2);
            setTeamList(tempList3);
            setIsLoading(false);
          })
      })
      .catch((error) => props.notify(error));
  };

  // 
  const loadFriendList = () => {
    console.log("load team.");
    const tempList = [];
    const option = {
      headers: {
        "Content-Type": "application/json",
      },
      withCredentials: true,
    };

    axios.get(`${window.location.origin}/api/users`, option)
      .catch(error=>{
        props.errorCheck(error.response);
      }) 
      .then((content) => {
        content["data"].map((user) => {
          const userInfo = {
            pk: user["pk"],
            username: user["username"],
            nickname: user["nickname"],
            email: user["email"],
          };
          // console.log("userInfo"+JSON.stringify(userInfo))
          if (user["username"] != username) tempList.push(userInfo);
        });
        setFriendList(tempList);
        //  console.log(JSON.stringify(tempList));
      })
      .catch(e=>props.notify(e))
  };


  //  modal  
  const teamAddToggle = () => {
    setTeamAddModal(!teamAddModal);
    setMyTeamName("");
  };
  // change
  const onChangeTeamName = (e) => {
    setMyTeamName(e.target.value);
  };

  // 
  const teamAdd = () => {
    const TeamNameData = {
      team_name: myteamName,
      team_leader: username,
    };
    console.log("team name data : ", TeamNameData);
    const body = JSON.stringify(TeamNameData);
    const option = {
      headers: {
        "Content-Type": "application/json",
      },
      withCredentials: true,
    };

    if (myteamName == "") {
      props.notify(" .");
      setMyTeamName("");
    } else {
      axios.post(`${window.location.origin}/api/team`, body, option)
        .catch(error=>{
          props.errorCheck(error.response);
          if(error.response.status>=400) throw Error(error.response.data['error']);
        }) 
        .then((content) => {
          console.log(content);
          console.log(JSON.stringify(TeamNameData));
          if (content.hasOwnProperty("error")) {
            throw Error("error");
          } else {
            setTeamAddModal(!teamAddModal);
            props.notify("  !");
            loadTeamList();
          }
        })
        .catch(e=>props.notify(e))
      setMyTeamName("");
      setTeamAddModal(!teamAddModal);
    }
  };


  //  modal on/off
  const teamInviteToggle = (e) => {
    setCurrentTeamID(e.target.value);
    setTeamInviteModal(!teamInviteModal);
  };

  //  
  const friendSearch = (name) => {
    if(name=='') return;
    if(typeof(name)=='object') name=friendName;
    console.log("search name : ", name);
    axios.get(`${window.location.origin}/api/search-user/${currentTeamId}/${name}`,option) //   
    .then(content => { 
      setFriendList(content.data);
    })
  }

  // 
  const teamInvite = () => {
    console.log("here!, frendChecked : ", friendChecked);
    for (let i = 0; i < friendChecked[0].length; i++) {
      const option = {
        headers: {
          "Content-Type": "application/json",
        },
        withCredentials: true,
        credentials: "include",
      };
      const userName = {
        username: friendList[friendChecked[0][i]]["username"],
      };
      console.log('invite check, ', friendList[friendChecked[0][i]]["username"]);
      const body = JSON.stringify(userName);
      axios.put(
          `${window.location.origin}/api/team/${currentTeamId}/invitation`,
          body,
          option
        )
        .catch(error=>{
          console.log("error : ", error.response.status);
          props.errorCheck(error.response, error.response.data['error']);
          if(error.response.status>=400) throw Error(error.response.data['error']);
        }) 
        .then((content) => {
          console.log("invitation : " + JSON.stringify(content));
          if (content.hasOwnProperty("error")) {
            props.notify("error");
          } else {
            props.notify(" !");
          }
        })
        .catch((e) => {
          props.notify(e);
        });
    }

    setTeamInviteModal(!teamInviteModal);
  };

  const onChangeFriendName = (e) => { //
    setFriendName(e.target.value);
    friendSearch(e.target.value);
  };

  //  
  const friendOnCheck = (value) => {
    console.log('select val : ', value, ', friend check list : ', friendChecked);
    friendChecked.pop();
    friendChecked.push(value);
    console.log(friendChecked[0]);
  };

 //  modal off
  const teamModifyToggle = () => {
    setTeamModifyModal(!teamModifyModal);
    setRenameTeamCheck(false);
    setMyTeamName("");
  };

  //  modal on
  const teamModify = (e) => {
    setCurrentTeamID(e.target.value);
    setCurrentTeamName(e.target.name);
    console.log(" id : " + e.target.value);
    console.log("  : " + e.target.name);

    axios.get(`${window.location.origin}/api/team-management/${e.target.value}`, option)
      .catch(error=>{
        props.errorCheck(error.response);
      }) 
      .then((content) => {
        let nameArr=[], nickArr=[], idArr=[];
        console.log(content['data'])
        setTeamLeader(content["data"]["team_leader"]);
        setTeamLeaderNick(content['data']['team_leader_nickname']);
        for(let idx in content['data']['member_list']){
          nameArr.push(content['data']['member_list'][idx]['username']);
          nickArr.push(content['data']['member_list'][idx]['nickname']);
          idArr.push(content['data']['member_list'][idx]['_id']);
        }
        setMemberList(nameArr);
        setMemberListNick(nickArr);
        setMemberListId(idArr);
        console.log(" : " + JSON.stringify(memberList));
        if (content["data"]["team_leader"] == username) {
          // console.log("isLeader : true");
          setIsLeader(true);
        } else {
          // console.log("isLeader : false");
          setIsLeader(false);
        }
        setTeamModifyModal(!teamModifyModal);
        setRenameTeamCheck(false);
        setMyTeamName("");
      })
      .catch(e=>props.notify(e));
  };

  //  modal on/off
  const teamLeaveToggle = () => {
    setTeamLeaveModal(!teamLeaveModal);
  };

  // 
  const teamLeave = () => {
    const option = {
      headers: {
        "Content-Type": "application/json",
      },
      withCredentials: true,
      credentials: "include",
    };
    console.log("  id : " + currentTeamId);
    axios.put(`${window.location.origin}/api/team/${currentTeamId}/secession`, null, option)
    .catch(error=>{
      props.errorCheck(error.response);
    }) 
    .then(() => {
      props.notify("");
      window.location.reload();
    })
    .catch(e=>props.notify(e))
    setTeamLeaveModal(!teamLeaveModal);
  };

  //  modal on/off
  const teamDeleteToggle = () => {
    setTeamDeleteModal(!teamDeleteModal);
  };
  // 
  const teamDelete = () => {
    const option = {
      headers: {
        "Content-Type": "application/json",
      },
      withCredentials: true,
      credentials: "include",
    };
    axios.delete(`${window.location.origin}/api/team-management/${currentTeamId}`, option)
      .catch(error=>{
        props.errorCheck(error.response);
      }) 
      .then((content) => {
        console.log("  : " + content);
        props.notify(".");
        loadTeamList();
      })
      .catch(e=>props.notify(e))
    setTeamDeleteModal(!teamDeleteModal);
    setTeamModifyModal(!teamModifyModal);
  };

  //  on/off
  const renameTeamToggle = () => {
    setRenameTeamCheck(!renameTeamCheck);
  };
  //  
  const renameTeam = () => {
    const data = {
      _id: currentTeamId,
      team_name: myteamName,
      team_leader: username,
    };

    axios.put(
        `${window.location.origin}/api/team-management/${currentTeamId}`,
        data,
        option
      )
      .catch(error=>{
        props.errorCheck(error.response);
      }) 
      .then((content) => {
        props.notify("  ");
        window.location.reload();
      })
      .catch(() => props.notify("  ."));
    setRenameTeamCheck(!renameTeamCheck);
  };

  const onChangeSearchKeyword=(e)=>{
    setSearchKeyword(e.target.value);
  }

  const submitKeyword=()=>{
    console.log("submit keyword, team ID : ", currentTeamId, ', keyword : ', searchKeyword);
    let url=`${window.location.origin}/api/${currentTeamId}/${searchKeyword}`;
    axios.get(url, option)
    .catch(error=>{
      props.errorCheck(error.response);
    }) 
    .then((content) => {
      console.log("content : ", content);
    })
    .catch((error) => props.notify(error));
  }

  return (
    <Fragment>
      <Container
        fluid
        className={classNames("round", "content", "team-content")}
        color="light">
        <div className="current-content">

          {/*--------------- Modal  ---------------*/}
          {/*   */}
          <Modal
            isOpen={teamAddModal}
            toggle={teamAddToggle}
            className="team-add-modal"
          >
            <ModalHeader className="team-add-header"> </ModalHeader>
            <ModalBody className="team-add-body">
              <span className="team-add-name">  : </span>
              <Input
                type="text"
                placeholder=""
                value={myteamName}
                onChange={onChangeTeamName}
                className="team-add-text"/>
            </ModalBody>
            <ModalFooter className="team-add-footer">
              <Button
                type="submit"
                onClick={teamAdd}
                className="team-add-button-create">
                
              </Button>
              <Button
                onClick={teamAddToggle}
                className="team-add-button-cancel">
                
              </Button>
            </ModalFooter>
          </Modal>

            <Modal
              isOpen={teamInviteModal}
              toggle={teamInviteToggle}
              className="team-invite-modal"
              backdrop="static">
            <ModalHeader className="modal-header"> </ModalHeader>
            <ModalBody>
            <div className="search-friend">
                  <InputGroup>
                    <Input
                      className="search-friend-input"
                      onChange={onChangeFriendName}
                      value={friendName}/>

                    <InputGroupAddon>
                      <Button
                        className="search-friend-icon-button"
                        onClick={friendSearch}>
                        <FontAwesomeIcon icon={faSearch} className="search-friend-icon" />
                      </Button>
                    </InputGroupAddon>
                  </InputGroup>
                  </div>
              <CustomTable>
                <BaseTable onCheck={friendOnCheck}>
                  <Tbody className="friend-list-body">
                    {friendList.map((friend, index) => (
                      <Tr key={index} value={friend["name"]}>
                        <Td>{index + 1}</Td>
                        <Td>{friend["nickname"]}</Td>
                        <Td>{friend["email"]}</Td>
                      </Tr>
                    ))}
                  </Tbody>
                </BaseTable>
              </CustomTable>
            </ModalBody>
            <ModalFooter className="modal-footer">
              <Button
                type="submit"
                color="primary"
                className="team-invite-button-invite"
                onClick={teamInvite}>
                
              </Button>
              <Button
                color="secondary"
                className="team-invite-button-cancel"
                onClick={teamInviteToggle}>
                
              </Button>
            </ModalFooter>
          </Modal>




          {/*  */}
          <Modal
            isOpen={teamModifyModal}
            toggle={teamModifyToggle}
            className="team-modify-modal"
          >
            <ModalHeader>  </ModalHeader>
            <ModalBody>
              <div className="team-rename ">
                <span className="team-rename-text ">  : </span>
                {!renameTeamCheck && (
                  <Fragment>
                    <Input
                      className="rename-input rename-disabled"
                      disabled={!renameTeamCheck}
                      placeholder={currentTeamName}
                      onChange={onChangeTeamName}/>
                    {isLeader && (
                      <Button
                        className="rename-icon-button"
                        onClick={renameTeamToggle}>
                        <FontAwesomeIcon icon={faPen} className="rename-icon" />
                      </Button>
                    )}
                  </Fragment>
                )}
                {renameTeamCheck && (
                  <Fragment>
                    <Input
                      className="rename-input"
                      disabled={!renameTeamCheck}
                      placeholder={currentTeamName}
                      onChange={onChangeTeamName}
                      value={myteamName}/>

                    <Button
                      className="rename-icon-button"
                      onClick={renameTeam}
                      onChange={onChangeTeamName}>
                      <FontAwesomeIcon icon={faPen} className="rename-icon" />
                    </Button>
                  </Fragment>
                )}
              </div>

              <div>
                <span className="modify-textS"></span>
              </div>
              <Table hover className="team-leader">
                <tbody className="team-leader-body">
                  <tr className="leader-item">
                    <th scope="row" />
                    <td>{teamLeaderNick} ({teamLeader})</td>
                    <td />

                    <td className="leader-table-button " />
                  </tr>
                </tbody>
              </Table>

              <div className="modify-textS">
                <span className="modify-textS"></span>
              </div>
              <Table hover className="member-list">
                <tbody className="member-table-body">
                  {memberListNick.map((member, index) => {
                    return (
                      <tr className="member-item" key={index}>
                        <th scope="row">{index + 1}</th>
                        <td>{member} ({memberList[index]})</td>
                        <td />

                        <td className="member-table-button thd-buttons">
                          {/* <Button
                        onClick={e => teamInviteToggle(e)}
                        className="team-invite-button"
                        value={member}
                        name=''>
                        
                      </Button> */}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </ModalBody>
            <ModalFooter>
              {!isLeader && (
                <Button onClick={teamLeaveToggle} className="leave-team-button">
                   
                </Button>
              )}
              {isLeader && (
                <Button
                  onClick={teamDeleteToggle}
                  className="leave-team-button">
                   
                </Button>
              )}
              <Button color="secondary" onClick={teamModifyToggle}>
                
              </Button>
            </ModalFooter>
          </Modal>


          {/* //  */}
          <Modal
            isOpen={teamLeaveModal}
            toggle={teamLeaveToggle}
            className="team-leave-modal"
          >
            <ModalBody className="team-leave-text"> </ModalBody>
            <ModalFooter className="modal-footer">
              <Button
                type="submit"
                color="primary"
                className="team-invite-button-invite"
                onClick={teamLeave}>
                
              </Button>
              <Button
                color="secondary"
                className="team-invite-button-cancel"
                onClick={teamLeaveToggle}>
                
              </Button>
            </ModalFooter>
          </Modal>



          {/* // */}
          <Modal
            isOpen={teamDeleteModal}
            toggle={teamDeleteToggle}
            className="team-leave-modal"
          >
            <ModalBody className="team-leave-text"> . </ModalBody>
            <ModalFooter className="modal-footer">
              <Button
                type="submit"
                color="primary"
                className="team-invite-button-invite"
                onClick={teamDelete}>
                
              </Button>
              <Button
                color="secondary"
                className="team-invite-button-cancel"
                onClick={teamDeleteToggle}>
                
              </Button>
            </ModalFooter>
          </Modal>

          <span className="content-name"></span>
          <div className="add-item">
            <Button onClick={teamAddToggle} className="add-team-button">
               
            </Button>
          </div>
        </div>





        {/*   */}
          <div className="team-table">
            <Table hover className="team-list">
              <tbody className="team-table-body">
                <tr>
                  <th />
                  <th></th>
                  <th></th>
                  <th></th>
                  <th className="thd-share"></th>
                  <th className="thd-buttons" />
                </tr>
                {isLoading ?
                  <Spinner size='lg' color='primary' className='team-spinner'/>
                :
                teamList.map((team, index) => {
                  return (
                    <tr className="team-item" key={index}>
                      <th scope="row">{index + 1}</th>
                      <td>{team["team_name"]}</td>
                      <td>{team["member_list"].length + 1}</td>
                      <td>{team["team_leader_nickname"]}<br/>({team["team_leader"]})</td> 
                      <td className="thd-share">{team["share_folders"].length}</td>
                      <td className="team-table-button thd-buttons">
                        {team["team_leader"] == username && (
                          <Button
                            onClick={(e) => teamInviteToggle(e)}
                            className="team-invite-button"
                            value={team["_id"]}
                            name={team["team_name"]}>
                            
                          </Button>
                        )}

                        <Button
                          onClick={teamModify}
                          className="team-modify-button"
                          value={team["_id"]}
                          name={team["team_name"]}>
                          
                        </Button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </Table>
          </div>

      </Container>
    </Fragment>
  );
};

export default TeamContent;
